<template lang="html">
  <div>
    <div v-if="connect()" style="padding-top: 100px;">
      
    </div>
      <div id="home">
          <div v-if="!connect()" class="ui inverted masthead centered segment">
            <div class="ui page grid">
              <div class="column">
                <div class="ui secondary pointing menu">
                  <a class="logo item" style="font-size:3em">
                    <b>MaFo</b>
                  </a>
                  <a class="active item">
                    <i class="flaticon-home"></i> Home
                  </a>
                  <a class="item">
                    <i class="flaticon-heart"></i> A propos
                  </a>
                  <div class="right menu">
                    <div class="item">
                      <div class="ui icon input">
                        <input placeholder="Search..." type="text">
                        <i class="flaticon-position link icon"></i>
                      </div>
                    </div>
                    <a class="ui item" href="#/login">
                      Login
                    </a>
                  </div>
                </div>
              </div>
            </div>
          </div>
            
          <center>
            <div class="ui buttons">
              <button @click.prevent ="chargerCarte" class="ui  button">Leaflet   </button>
              <div class="or" data-text="ou"></div>
            <button @click.prevent="chargerCarte()" class="ui  positive button">Google Map</button>
            </div>
        </center>
        <div>
        <div v-show="choix" id="map" style="padding-top: 100px;">
          <div>
            <p>Waiting....</p>
          </div>
        </div>
        <div v-show="!choix" id="mapid" style="padding-top: 100px;">
          <div>
            <p>Waiting....</p>
          </div>
        </div>
        <!-- src="http://lorempixel.com/500/200/nature" -->
        <hr>
        <div style="height:400px;">

           <div class="ui three column grid">
          <div class="column">
            <div class="ui segment">
              <div class="item">
                <div class="image">
                  <img src="../../assets/images/g7.jpg">
                </div>
                <div class="content">
                  <a class="header">Nos engins</a>
                  <div class="meta">
                    <span class="cinema">Mecanic</span>
                  </div>
                  <div class="description">
                    <p></p>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="column">
            <div class="ui segment">
              <div class="item">
                <div class="image">
                  <img src="../../assets/images/g6.jpg">
                </div>
                <div class="content">
                  <a class="header">Nos sites</a>
                  <div class="meta">
                    <span class="cinema">Routes</span>
                  </div>
                  <div class="description">
                    <p></p>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="column">
            <div class="ui segment">
              <div class="item">
                <div class="image">
                  <img src="../../assets/images/g1.jpg">
                </div>
                <div class="content">
                  <a class="header">Matériel de Foumban</a>
                  <div class="meta">
                    <span class="cinema">MAFO</span>
                  </div>
                  <div class="description">
                    <p></p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div> 
        </div>
       <hr>
          <div class="ui inverted masthead centered segment" style="height: 100px;">
            <center> <u>Contacts :</u>+237 695 15 95 63</center>
          </div>
      </div>
    </div>
  </div>
</template>

<script>
import {loginUrl,siteShowAll, userUrl, getHeader} from '../../config'
import vuex from 'vuex'
import _ from 'lodash'
import toastr from '../../assets/toastr.min.js'
var randomColor = require('random-color')

//import homer from '../../assets/home.js'
 var localStorage = require('web-storage')().localStorage 

export default {
  data() {
    return {
      choix: true,
     showMenu: true,
      loading: true,
      loaded: false,
      sites: [],
      connect : function() {
        if (localStorage.get("authUser")) {
          return true
        }else{
          return false
        }
      },
      site: {
        nom:  "",
        description:  "",
        image: null,
        imageExt: null
      },
      lat:0,
      lng:0,
      map: true,
      drawingManager: null,
      selectedShape: null,
      pathsWM: [],
      cartLoading: true,
      sending: false
    };
  },
  created () {},
  computed: {},
  ready() {
    console.log("hlkjb")
    setTimeout(() => {
      this.check()
    },1000);
  },
  mounted(){
    console.log("hlkjb")
    setTimeout(() => {
      this.check()
    },1000);
    this.chargerSite()
  },
  attached() {},
  methods: {
    chargerSite () {
      this.$http.get(siteShowAll).then(data => {
          console.log(data.body)
          this.sites = data.body
      
          toastr.success("les sites sont chargés ")
         
        
      },data => {
        toastr.error("Impossible de charger les sites!!")
      })
    },
    chargerCarte () {
      this.choix = !this.choix
    },
    check(){
      console.log("test");
      if(typeof google !=='undefined'){
        console.log("test2");
        this.createMap()
      }else{
        //setTimeout(this.check,1000);
      }
    },
    createMap(){
      console.log("test");
      this.cartLoading = false
      var map = new google.maps.Map(document.getElementById('map'), {
          zoom: 8,
          center: new google.maps.LatLng(5.729063, 10.859204),
          mapTypeId: google.maps.MapTypeId.ROADMAP,
          disableDefaultUI: false,
          zoomControl: true
      })

       _.forEach(this.sites, (value) => {
        // console.log(value)
         var monPolygone = new google.maps.Polygon({
                        paths:value.coord,
                        strokeColor: '#FFF',
                        strokeOpacity: 0.8,
                        strokeWeight: 3,
                        fillColor: randomColor(0.99, 0.99).hexString(),
                        fillOpacity: 0.35
        });
         // monPolygone.setMap(map)

         var tab= []
          for(var i in value.coord ){
                  var london = new google.maps.LatLng(value.coord[i].lat, value.coord[i].lng);
                  tab[i]= london;
                  
              }
          var area = google.maps.geometry.spherical.computeArea(tab)
          console.log('surface ='+area)
           var bounds = new google.maps.LatLngBounds()
           var i;
              for (i = 0; i < tab.length; i++) {
                            bounds.extend(tab[i]);
               }
               var centre = bounds.getCenter()
               //on positionne les marquers au milieu de chaques sites
               var marker= new google.maps.Marker({
             position: centre,
             map: map,
             title: "clicker sur ce marquer pour plus d'infomations",
              animation: google.maps.Animation.DROP,
             draggable: false 
         });
          var contentString = '<div id="content">'+
            '<div id="siteNotice">'+
            '</div>'+
            '<h1 id="firstHeading" class="firstHeading">site de : '+ value.site.nom +'</h1>'+
            '<div id="bodyContent"><u>Description</u>'+
            '<p> '+ value.site.description +' </p><p><u></u>'+
            '<h2>'+ area +'  (M<sup>2</sup>)</h2></p>'+
            '<p>Attribution: Uluru, <a href="/admin/site/'+value.site.id+'">'+
            'https://en.wikipedia.org/w/index.php?title=Uluru</a> Superficie: <u>'+ area +'</u></p>'+
            '</div>'+
            '</div>';

             var infoWindow = new google.maps.InfoWindow({
                                 content  : contentString,
                                 position : centre
                               });
              google.maps.event.addListener(marker, 'click', function() {
                                 infoWindow.open(map,marker);
                               });



         monPolygone.setMap(map)
      });

      // var polyOptions = {
      //     strokeWeight: 0,
      //     fillOpacity: 0.45,
      //     editable: true,
      //     draggable: false,
      //     fillColor: '#0000ff'
      // }
      // var monPolygone = new google.maps.Polygon({
      //                   paths:tableauPointsPolygone,
      //                   strokeColor: '#FFF',
      //                   strokeOpacity: 0.8,
      //                   strokeWeight: 3,
      //                   fillColor: coul[m],
      //                   fillOpacity: 0.35
      //   });









    },
  },
  }
  components: {}

</script>
<style src="../../assets/home.css" scoped>
  #map:{
    height: 00px;

  }

</style>



